.portfolio-bg {
  width: 100%;
  min-height: 100vh;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;

  gap: 5rem;

  .portfolio-title {
    text-align: center;
    margin-top: 2rem;
    position: relative;
    z-index: 1;

    h1 {
      color: white;
      font-size: 5rem;
    }
    h2 {
      color: white;
      font-size: 2rem;
      margin-top: 10rem;
      margin-bottom: 5rem;
      z-index: 3;
      background-color: black;
      padding: 1rem 0;
    }

    span {
      height: 2px;
      width: 800px;
      position: absolute;
      top: 73%;
      left: -75%;
      background-color: white;
      z-index: -1;
    }
  }
  .portfolio-box {
    max-width: 1800px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 20rem;
    column-gap: 5rem;
    margin: 0 18rem;
    margin-bottom: 15rem;

    .portfolio-card {
      background-color: white;
      width: 500px;
      height: 300px;

      .portfolioImg {
        height: 100%;
        width: 100%;

        .portfolioImg01 {
          background-image: url(../img/portfolio01.png);
          background-size: cover;
          background-position: center;
          height: 100%;
          width: 100%;
          transition: 0.5s;
          position: relative;
          overflow: hidden;
          .imgfilter01 {
            position: absolute;
            top: -100%;
            left: 0;
            height: 100%;
            width: 100%;
            transition: 0.5s;
            background: linear-gradient(transparent, rgb(58, 226, 229));

            .img-box01 {
              width: 100%;
              height: 100%;
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: flex-end;
              gap: 3rem;
              img {
                width: 80px;
                background-color: rgba(255, 255, 255, 0.5);
                padding: 1rem;
                border-radius: 10px;
                cursor: pointer;
              }
              .img-content01 {
                display: flex;
                gap: 2rem;
                color: rgb(5, 5, 47);
                margin-bottom: 1rem;
              }
            }
          }
        }
        .portfolioImg01:hover {
          transform: scale(1.3);
          .imgfilter01 {
            background: linear-gradient(transparent, rgb(58, 226, 229));
            top: 0;
          }
        }

        .portfolioImg02 {
          background-image: url(../img/portfolio02.png);
          background-size: cover;
          background-position: center;
          height: 100%;
          width: 100%;
          transition: 0.5s;
          overflow: hidden;
          position: relative;
          .imgfilter02 {
            position: absolute;
            top: -100%;
            left: 0;
            height: 100%;
            width: 100%;
            transition: 0.5s;
            background: linear-gradient(transparent, rgb(217, 101, 211));

            .img-box02 {
              width: 100%;
              height: 100%;
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: flex-end;
              gap: 3rem;

              img {
                width: 80px;
                background-color: rgba(255, 255, 255, 0.5);
                padding: 1rem;
                border-radius: 10px;
                cursor: pointer;
              }
              .img-content02 {
                display: flex;
                gap: 2rem;
                color: rgb(5, 5, 47);
                margin-bottom: 1rem;
              }
            }
          }
        }
        .portfolioImg02:hover {
          transform: scale(1.3);
          .imgfilter02 {
            background: linear-gradient(transparent, rgb(217, 101, 211));
            top: 0;
          }
        }
        .portfolioImg03 {
          background-image: url(../img/portfolio03.png);
          background-size: cover;
          background-position: center;
          height: 100%;
          width: 100%;
          transition: 0.5s;
          overflow: hidden;
          position: relative;
          .imgfilter03 {
            position: absolute;
            top: -100%;
            left: 0;
            height: 100%;
            width: 100%;
            transition: 0.5s;
            background: linear-gradient(transparent, rgb(229, 169, 58));
            .img-box03 {
              width: 100%;
              height: 100%;
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: flex-end;
              gap: 3rem;
              img {
                width: 80px;
                background-color: rgba(255, 255, 255, 0.5);
                padding: 1rem;
                border-radius: 10px;
                cursor: pointer;
              }
              .img-content03 {
                display: flex;
                gap: 2rem;
                color: rgb(5, 5, 47);
                margin-bottom: 1rem;
              }
            }
          }
        }
        .portfolioImg03:hover {
          transform: scale(1.3);
          .imgfilter03 {
            background: linear-gradient(transparent, rgb(229, 169, 58));
            top: 0;
          }
        }
      }
    }

    .card-1 {
      border: 3px solid rgb(58, 226, 229);
      box-shadow: 0 0 3rem rgb(58, 226, 229);
      position: relative;
    }
    .card-1::before {
      content: "";
      position: absolute;
      background-color: rgb(58, 226, 229);
      top: 100%;
      left: 0;
      width: 100%;
      height: 100%;
      transform: perspective(5em) rotateX(40deg) scale(1, 0.3);
      filter: blur(3em);
    }
    .card-2 {
      border: 3px solid rgb(217, 101, 211);
      box-shadow: 0 0 3rem rgb(217, 101, 211);
      position: relative;
    }
    .card-2::before {
      content: "";
      position: absolute;
      background-color: rgb(217, 101, 211);
      top: 100%;
      left: 0;
      width: 100%;
      height: 100%;
      transform: perspective(5em) rotateX(40deg) scale(1, 0.3);
      filter: blur(3em);
    }
    .card-3 {
      border: 3px solid rgb(229, 169, 58);
      box-shadow: 0 0 3rem rgb(229, 169, 58);
      position: relative;
    }
    .card-3::before {
      content: "";
      position: absolute;
      background-color: rgb(229, 169, 58);
      top: 100%;
      left: 0;
      width: 100%;
      height: 100%;
      transform: perspective(5em) rotateX(40deg) scale(1, 0.3);
      filter: blur(3em);
    }
  }
}
